  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const scatterPlot = new Scatter('container', {
      appendPadding: 30,
      data,
      xField: 'changeInFemaleRate',
      yField: 'changeInMaleRate',
      sizeField: 'pop',
      colorField: 'continent',
      color: ['#ffd500', '#82cab2', '#193442', '#d18768', '#7e827a'],
      size: [4, 30],
      shape: 'circle',
      pointStyle: {
        fillOpacity: 0.8,
        stroke: '#bbb'
      },
      xAxis: {
        min: -25,
        max: 5,
        grid: {
          line: {
            style: {
              stroke: '#eee'
            }
          }
        },
        line: {
          style: {
            stroke: '#aaa'
          }
        }
      },
      yAxis: {
        line: {
          style: {
            stroke: '#aaa'
          }
        }
      },
      quadrant: {
        xBaseline: 0,
        yBaseline: 0,
        labels: [
          {
            content: 'Male decrease,\nfemale increase'
          },
          {
            content: 'Female decrease,\nmale increase'
          },
          {
            content: 'Female & male decrease'
          },
          {
            content: 'Female &\n male increase'
          }
        ]
      }
    })
    scatterPlot.render()

    this.containerGs()
    this.containerGz()
  },
  methods: {
    containerGs() {
      const data = this.datas
      const scatterPlot = new Scatter('containerGs', {
        appendPadding: 30,
        data,
        xField: 'x',
        yField: 'y',
        colorField: 'genre',
        color: [
          'r(0.4, 0.3, 0.7) 0:rgba(255,255,255,0.5) 1:#5B8FF9',
          'r(0.4, 0.4, 0.7) 0:rgba(255,255,255,0.5) 1:#61DDAA'
        ],
        sizeField: 'size',
        size: [5, 20],
        shape: 'circle',
        yAxis: {
          nice: true,
          line: {
            style: {
              stroke: '#eee'
            }
          }
        },
        xAxis: {
          grid: {
            line: {
              style: {
                stroke: '#eee'
              }
            }
          },
          line: {
            style: {
              stroke: '#eee'
            }
          }
        }
      })
      scatterPlot.render()
    },
    containerGz() {
      const data = [
        {
          city: '上海',
          UV: 1.5,
          DAU: 6,
          DAUs: 3
        },
        {
          city: '台北',
          UV: 2,
          DAU: 5,
          DAUs: 13
        },
        {
          city: '北京',
          UV: 7,
          DAU: 3.6,
          DAUs: 16
        },
        {
          city: '济南',
          UV: 5,
          DAU: 5,
          DAUs: 16
        },
        {
          city: '青岛',
          UV: 2,
          DAU: 1,
          DAUs: 19
        },
        {
          city: '杭州',
          UV: 7,
          DAU: 2,
          DAUs: 90
        },
        {
          city: '广东',
          UV: 7.4,
          DAU: 1.5,
          DAUs: 30
        },
        {
          city: '无锡',
          UV: 1,
          DAU: 1,
          DAUs: 34
        },
        {
          city: '重庆',
          UV: 7,
          DAU: 5,
          DAUs: 46
        },
        {
          city: '成都',
          UV: 3.4,
          DAU: 2.3,
          DAUs: 49
        },
        {
          city: '哈尔滨',
          UV: 0.5,
          DAU: 6.5,
          DAUs: 51
        },
        {
          city: '内蒙古',
          UV: 2.5,
          DAU: 5,
          DAUs: 51
        },
        {
          city: '云南',
          UV: 1,
          DAU: 5,
          DAUs: 53
        },
        {
          city: '河北',
          UV: 6,
          DAU: 5,
          DAUs: 57
        },
        {
          city: '陕西',
          UV: 2,
          DAU: 3,
          DAUs: 57
        },
        {
          city: '苏州',
          UV: 3,
          DAU: 4.6,
          DAUs: 65
        },
        {
          city: '四川',
          UV: 6,
          DAU: 7,
          DAUs: 68
        },
        {
          city: '贵阳',
          UV: 5,
          DAU: 3.4,
          DAUs: 68
        },
        {
          city: '台湾',
          UV: 5,
          DAU: 2,
          DAUs: 69
        },
        {
          city: '哈尔滨',
          UV: 2,
          DAU: 7,
          DAUs: 78
        },
        {
          city: '天津',
          UV: 4.4,
          DAU: 5,
          DAUs: 45
        },
        {
          city: '长沙',
          UV: 3.4,
          DAU: 7,
          DAUs: 29
        },
        {
          city: '沧州',
          UV: 3,
          DAU: 1,
          DAUs: 94
        },
        {
          city: '宁波',
          UV: 6,
          DAU: 3,
          DAUs: 99
        }
      ]
      const scatterPlot = new Scatter('containerGz', {
        width: 800,
        height: 400,
        autoFit: false,
        appendPadding: 16,
        data,
        xField: 'UV',
        yField: 'DAU',
        sizeField: 'DAUs',
        size: [12, 30],
        shape: 'circle',
        pointStyle: {
          fill: '#D6E3FD',
          fillOpacity: 0.6,
          stroke: '#6d9bf9'
        },
        tooltip: {
          showTitle: true,
          showMarkers: false,
          fields: ['UV', 'DAU', 'DAUs'],
          customContent: (title, items) => {
            const field = items ? items[0] : []
            const formatterInfo = {
              UV: (value) => value + '万',
              DAU: (value) => value + '万',
              DAUs: () => '%'
            }
            let htmlStr = `<div style="margin:10px 0;font-weight:700;">${field ? (field.data ? field.data.city : '') : ''}</div><div class="g2-tooltip-items">`
            items.forEach((item) => {
              htmlStr += `<div class="g2-tooltip-item" style="margin-bottom:8px;display:flex;justify-content:space-between;">
                      <span class="g2-tooltip-item-label" style="margin-right: 12px;">${item.name}</span>
                      <span class="g2-tooltip-item-value">${item.value + formatterInfo[item.name](item.value)}</span>
                    </div>`
            })
            htmlStr += '</div>'
            return htmlStr
          }
        },
        xAxis: {
          grid: {
            line: {
              style: {
                stroke: '#eee'
              }
            }
          },
          label: {
            formatter: (v) => (v !== '0' ? v + '%' : v)
          },
          line: null
        },
        label: {
          formatter: (item) => {
            return item.city
          },
          offsetY: 12,
          style: {
            fontSize: 12,
            fill: 'rgba(0,0,0,0.85)'
          }
        },
        yAxis: {
          min: 0,
          line: null,
          label: {
            formatter: (v) => (v !== '0' ? v + '%' : v)
          }
        },
        annotations: [
          {
            type: 'text',
            position: [4, 8],
            content: 'DAUs',
            offsetY: -8,
            style: {
              fontSize: 12,
              textAlign: 'center'
            }
          },
          {
            type: 'text',
            position: [8, 4],
            content: 'DAUs',
            rotate: Math.PI / 2,
            offsetY: -40,
            offsetX: 8,
            style: {
              fontSize: 12
            }
          },
          {
            type: 'region',
            start: [7, 7],
            end: [7.8, 7.8],
            top: true,
            style: {
              fill: '#fff',
              fillOpacity: 0.5,
              opacity: 1
            }
          },
          {
            type: 'region',
            start: [0.2, 7],
            end: [1, 7.8],
            top: true,
            style: {
              fill: '#fff',
              fillOpacity: 0.5,
              opacity: 1
            }
          },
          {
            type: 'region',
            start: [7, 0.2],
            end: [7.8, 1],
            top: true,
            style: {
              fill: '#fff',
              fillOpacity: 0.5,
              opacity: 1
            }
          }
        ],
        quadrant: {
          xBaseline: 4,
          yBaseline: 4,
          lineStyle: {
            lineDash: [4, 2],
            lineWidth: 2
          },
          regionStyle: [
            {
              fill: '#5bd8a6',
              fillOpacity: 0.1
            },
            {
              fill: '#667796',
              fillOpacity: 0.1
            },
            {
              fill: '#fff'
            },
            {
              fill: '#f7664e',
              fillOpacity: 0.1
            }
          ],
          labels: [
            {
              content: '热门市场',
              position: [7.2, 7],
              style: {
                fill: 'rgba(0,0,0, 0.85)',
                textAlign: 'start'
              }
            },
            {
              content: '潜力市场',
              position: [0.2, 7],
              style: {
                fill: 'rgba(0,0,0, 0.85)',
                textAlign: 'start'
              }
            },
            {
              content: ''
            },
            {
              content: '提频市场',
              position: [7.2, 1],
              style: {
                fill: 'rgba(0,0,0, 0.85)',
                textAlign: 'start'
              }
            }
          ]
        }
      })
      scatterPlot.render()
    }
  },
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 800px;
    height: 400px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



